import React from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import Home from "./Home";
import Service from "./Service";
import About from "./About";
import Contact from "./Contact";
import Navbar from "./Navbar";
import Footer from "./Footer";
import { Redirect, Route, Switch } from 'react-router';


const App = () => {
   return(
       <>
       <Navbar />   {/* using navbar by bootstrap5 */}
       <Switch>     {/*diff url me switch krne ke liye*/}
         <Route exact path ="/" component = {Home} />   {/* URL*/} {/* By defult exact path dege Homw componenet ke liye */}
         <Route exact path ="/about" component = {About} />
         <Route exact path ="/service" component = {Service} />
         <Route exact path ="/contact" component = {Contact} />
         <Redirect to = "/" />   {/*jo path hum dete hain vo agar na hoto vo Home page me redirect kr dega*/}
       </Switch>
       <Footer />
       </>
   )
}

export default App;